@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;900&display=swap);
*{
  margin:0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.header{
    background-color: #171717;
    position: relative;
    box-shadow: 0 0 15px rgba(0, 0, 0, .5)

    
}
.header-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    height: 90px;
}
.brand-name{
    margin-left: 150px;
    font-weight: 700;
    letter-spacing: 2px;
    cursor: pointer;
}
.home-link{
    text-decoration: none;
    color: white;
}
.user-cart{
    margin-right: 100px;
    flex-grow: .2;
    display: flex;
 justify-content: space-around;
 align-items: center;
 cursor: pointer;
}

.cart{
    position: relative;
}
.cart span{
    position: absolute;
    color: black;
    background-color: white;
    border-radius: 20px;
    padding: 3px 6px;
    left:-5px;
    top:-15px;
    font-weight: 600;
    font-size: 16px;
}
.cart i:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: all 300ms ease;

}
.user span{
    font-size: 20px;
    padding-left: 5px;
}

.menu{
    background-color: #171717;
    color: white;
    height: 130vh;
    width: 22%;
    position: absolute;
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    text-align: center;
    transition: all 400ms ease-in;
    box-shadow: 10px 0 15px -15px rgba(0, 0, 0, .5);
 

}
.menu .nav-link{
    text-decoration: none;
    color:white;
    font-size: 1.5rem;
    font-weight: 600;
    width: 100%;
    margin-bottom: 30px;
    padding: 10px 0;
    transition: 300ms all ease;
    cursor: pointer;
}
.menu .nav-link:hover{
    background-color: white;
    color: #171717;
    border-top-left-radius:20px ;
    border-bottom-left-radius:20px ;
    box-shadow: 10px 10px 15px  rgba(0, 0, 0, .2)inset

}

.checkout-cart{
   background-color: #171717;
   position: absolute;
   right: 10%;
   height: 200px;
   min-width: 200px;
   color: white;
   padding: 20px 10px 0;
   border-bottom-left-radius: 5px;
   border-bottom-right-radius: 5px;
   overflow-y:scroll;
}

.subtotal{
    width: 50%;
    margin: 20px auto;
}
.checkout-product{
    width: 300px;
    display: flex;
    margin: 10px 0;
    color: black;
    background-color: white;
    padding: 5px 10px;
    border-radius: 5px;


}
.checkout-product img{
    max-width: 50px;
}
.checkout-product-info{
    display: flex;
    flex: 1 1;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 20px;
}
.remove-from-cart{
    background-color: #171717;
    color: white;
    border: none;
    padding: 5px;
    width: 30%;
    margin-left: 50%;
    cursor: pointer;
}
.home{
    max-width: 1200px;
    margin-left: 23%;
    
}

.home-text{
    background-color: #FDF6F0;
    width: 50%;
    height: 400px;
    margin: auto;
    margin-top: 100px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    border-radius: 20px;
}

.shop-now{
    width: 40%;
    margin: 50px auto;
    border-radius: 0;
    height: 50px;
    border: none;
    background-color: #706C61;
    color: white;
    cursor: pointer;
}
.shop-now:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 300ms all ease;
    box-shadow: 0 0 15px rgba(0,0,0,0.3);
}
.shop-now-link{
    text-decoration: none;
    color: white;
}
.home-heading{
    margin-bottom: 50px;
    font-size: 3rem;
    color: #706C61;
}
.home-heading span{
    color: black;
}
.home-tagline{
    font-size: 1.3rem;
    color: #706C61;
}

.product{
   max-width: 250px;
   display: flex;
   flex: 1 1;
   flex-direction: column;
   box-shadow: 0 0 15px rgba(0, 0, 0, .1);
   text-align: center;
   margin: 20px 20px;  
   
}

.product-name{
    margin: 5px 0;
    font-size: 20px;
}
.product-info,.product-cost{
    margin: 5px 0;
}
.product-image{
    max-width: 200px;
    margin: 20px auto;
}
.add-to-cart{
    width: 50%;
    border: 1px solid gray;
    border-radius: 0;
    background-color: transparent;
    padding: 5px;
    margin: 5px auto;
    cursor: pointer;
   
}
.product-view-link{
    text-decoration: none;
    color: black;
    text-align: center;
}
.shoes{

    max-width: 1200px;
    margin-left: 23%;
}
.shoes h1{
    margin: 30px 20px 0;
}
.product-section{
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-between; */
}
.masks{
    
    max-width: 1200px;
    margin-left: 23%;
}
.masks h1{
    margin: 30px 20px 0;
}
.bags{
    
    max-width: 1200px;
    margin-left: 23%;
}
.bags h1{
    margin: 30px 20px 0;
}
.product-view{
    max-width: 1200px;
    margin-left: 23%;
    margin-top: 20px;
    width: 1100px;
    height: 90vh;
    background-color: #F7F6F2;
    padding-left: 30px;
    box-shadow: 0 0 15px rgba(0,0,0,0.2);
}
.product-view-page-heading{
    color: #2B2B2B;
}
.product-view-container{
    margin-top: 50px;
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    color: #2B2B2B;
}
.product-view .product-name{
    font-size: 4rem;
}
.product-view-info{
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}
.product-view .product-info{
    font-size: 2rem;
}
.product-view .add-to-basket{
    margin-top: 50px;
    height: 50px;
    background-color: transparent;
    border: 1px solid black;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    width: 30%;
}
